.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button {
  width: 100%;
  margin-top: 1rem;
}

.promocode {
  position: relative;
  display: block;
  color: var(--link-color);
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 2rem;
  cursor: pointer;
}

.promocode > input {
  position: absolute;
  visibility: hidden;
  width: 100%;
  height: 100%;
}
